const numFromUserInput = document.querySelector("#random-num input");
//const numFromUserVal = numFromUserInput.value; //Ïà´Ïûê ÏµúÎåÄ Î≤îÏúÑÏúÑ
const guessInput = document.querySelector("#guessInput");
const submitBtn = document.getElementById("submitBtn"); //play Î≤ÑÌäº
const resultText = document.getElementById("resultText"); //resultÍ∞í Îì§Ïñ¥Ïò¨ div

//let randomNum;

// ÏäπÎ∂Ä Ïù¥ÍπÄ vs Ïßê ?
function bettingNumber(num1, num2) {
  console.log(`num1 ${num1}, num2 ${num2}`);
  if (num1 == num2) {
    // === ÏÇ¨Ïö©ÌñàÏùÑÎïê ifÏ†à ÏïàÌÉîÏùåÏùå
    //Ïù¥ÍπÄ
    console.log("win");
    resultText.innerHTML += `<h3>YOU WIN!</h3>`;
  } else {
    console.log("lose");
    resultText.innerHTML += `<h3>YOU LOST!</h3>`;
  }
}

function reset() {
  numFromUserInput.value = "";
  guessInput.value = "";
}

//Ïú†Ìö®ÏÑ± Ï≤¥ÌÅ¨
function verification(guessNumUser, numFromUser) {
  if (guessNumUser > numFromUser) {
    alert("Ïú†Ìö®Ìïú Ïà´ÏûêÎ°ú Îã§Ïãú ÏûÖÎ†•ÌïòÏÑ∏Ïöî");
    reset();
    return false;
  }
  if (guessNumUser < 0) {
    alert("ÏñëÏàòÎ°ú Îã§Ïãú ÏûÖÎ†•ÌïòÏÑ∏Ïöî");
    reset();
    return false;
  }
  return true;
}

function submitEvent(e) {
  e.preventDefault(); // Ìï¥ÎãπÏù¥Î≤§Ìä∏ Í∞ùÏ≤¥Ïùò Í∏∞Î≥∏ Ïã§ÌñâÏùÑ ÏùºÎã® ÎßâÏùå
  const numFromUser = numFromUserInput.value;
  const guessNumUser = guessInput.value;
  console.log("guessNumUser" + guessNumUser);
  // Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨
  if (!verification(guessNumUser, numFromUser)) {
    return false;
  }

  let randomNum;
  randomNum = Math.round(Math.random() * (numFromUser - 0) + 0);

  resultText.innerText = `You chose : ${guessNumUser}, the machine chose : ${randomNum}`;
  bettingNumber(guessNumUser, randomNum);
}

submitBtn.addEventListener("click", submitEvent);

